/* ______________________________________________________________
пробую все красивенько зробити
_______________________________________________________________ */

html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  perspective: 1px;
  transform-style: preserve-3d;
  overflow-y: scroll;
  overflow-x: hidden;
  font-family: "Amatic SC", cursive;
  color: #fff;
}

.home_page {
  box-sizing: border-box;
  min-height: 100vh;
  text-align: center;
  padding-top: 20%;
  transform-style: inherit;
  height: 100vw;
}

.home_page::before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: -1;
  transform-origin: center;
  min-height: 100vh;
  background-image: url("https://ua.doogee.cc/data/attachment/image/20190221/3b640311d406965a92d5582890196e42.jpg?filesize=155.71");
  background-repeat: no-repeat;
  background-size: cover;

  /* Паралакс ефект */
  transform: translateZ(-1px) scale(2);
}

.home_page h1 {
  margin-top: -100px;
  font-size: 42px;
}

.navlink {
  color: #e0e0e0;
  text-decoration: none;
}

.table {
  background: #e1eaee9c;
}

/* ______________________________________________________________
phoneCatalog
_______________________________________________________________ */
.catalog {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(22px, 230px));
  padding: 0;
  margin: 0;
  justify-content: center;
}

.price-tags {
  display: flex;
  justify-content: space-between;
  color: #0e0d0d;
  font-family: "Roboto", sans-serif;
}

.price-tag {
  background: white;
  width: 200px;
  margin: 0 0 25px;
  position: relative;
  height: 275px;
  padding: 10px;
}

.price-tag:hover {
  box-shadow: 0 7px 24.3px 2.7px rgba(91, 89, 89, 0.14);
}

.price-tag__wrapp-card {
  width: 100px;
  display: flex;
  padding-top: 0;
  padding-left: 53px;
  margin-bottom: 10px;
}

.price-tag__wrapp-card-img {
  overflow: hidden;
  box-sizing: border-box;
  object-fit: cover;
  width: 100%;
  position: relative;
  display: block;
}

.card__information__detail {
  position: absolute;
  bottom: 0;
  padding: 13px 25px;
  font-size: 10px;
  color: #212020;
  width: 150px;
}

.default__btn {
  background: linear-gradient(55deg, #1c1b1d, transparent);
  border-radius: 10px;
}

.default__btn:hover {
  border: 3px solid #757575;
  background: linear-gradient(0.5deg, #1c1b1d, transparent);
}

.card__item-title {
  color: #212020;
  font-size: 18px;
  text-decoration: none;
  padding: 14px 10;
}

.about__item-text {
  font-size: 12px;
  letter-spacing: 0.1px;
  padding: 10px 0;
}

.price-btn {
  width: 100%;
  display: flex;
  color: white;
  justify-content: center;
}

